import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useLocation } from 'react-router-dom'
import {
  CHeader,
  CToggler,
  CSubheader,
  // CBreadcrumbRouter,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CBreadcrumb,
  CBreadcrumbItem,
} from '@coreui/react'

// routes config
// import routes from '../routes'
import { userLogout } from 'apiServices'

const TheHeader = (props) => {
  const dispatch = useDispatch()
  const location = useLocation()

  //source area
  const stockTakeArea = useSelector(state => state.stockTakeEntryData?.jobPost?.areaName)
  const stockTransferArea = useSelector(state => state.stockTransferEntryData?.jobPost?.areaName)
  const wasteArea = useSelector(state => state.wasteEntryData?.jobPost?.areaName)
  const repackArea = useSelector(state => state.repackEntryData?.jobPost?.areaName)
  const stockMovementArea = useSelector(state => state.stockMovementEntryData?.jobPost?.areaName)
  const outboundArea = useSelector(state => state.outboundEntryData?.jobPost?.areaName)

  //destionation area
  const repackDestinationArea = useSelector(state => state.repackEntryData?.jobPost?.areaDestinationName)
  const stockMovementDestinationArea = useSelector(state => state.stockMovementEntryData?.jobPost?.areaDestinationName)

  // const asideShow = useSelector(state => state.asideShow)
  // const darkMode = useSelector(state => state.darkMode)
  const user = useSelector(state => state.user)
  const sidebarShow = useSelector(state => state.sidebarShow)

  const toggleSidebarMobile = () => {
    const val = [false, 'responsive'].includes(sidebarShow) ? true : 'responsive'
    dispatch({ type: 'set', sidebarShow: val })
  }

  //route path
  const [path, setPath] = useState()

  //restructure path
  useEffect(() => {
    let paths = location?.pathname?.split('/')

    //experiment
    let breadcrumpData = []
    let loop = paths.map((data, index) => {

      //split url to text
      let text = data.split('-')

      text = text.map(t => {
        const txt = t.charAt(0).toUpperCase() + t.slice(1)
        return txt
      })
      let tmp = []
      let txt = text.join(' ')
      let old_url = (breadcrumpData && breadcrumpData[index - 1]) ? breadcrumpData[index - 1]?.url + '/' : ''

      //add to breadcrump array
      if (paths[index - 1] == "inventory-entry") {
        tmp.url = breadcrumpData[index - 1].url
      } else {
        tmp.url = old_url + paths[index]
      }
      tmp.class = 'LatoBold'
      if (paths.length == 3 && data == 'inventory-entry' && index == 1) {
        tmp.text = txt + '  -  '
        tmp.class += ' green'
        tmp.url += '/' + paths[index + 1]
      } else if (paths.length > 3 && data == 'inventory-entry' && index == 1) {
        tmp.text = txt + '  -  '
        tmp.url += '/' + paths[index + 1]
      } else if ((index + 1) < paths.length) {
        tmp.text = txt + '  /  '
      } else {
        tmp.text = txt
      }

      if ((index + 1) == paths.length) {
        tmp.class += ' green'
      }
      breadcrumpData.push(tmp)
    })
    breadcrumpData.shift();

    //add area in weight entry
    const activeModule = location?.pathname?.split('/')
    let area = ''
    let destinationArea = null
    switch (activeModule[2]) {
      case 'stock-take':
        area = stockTakeArea
        break
      case 'movement':
        area = stockMovementArea
        destinationArea = stockMovementDestinationArea
        break
      case 'waste':
        area = wasteArea
        break
      case 'additional-outbound':
        area = outboundArea
        break
      case 'repacking':
        area = repackArea
        destinationArea = repackDestinationArea
        break
      case 'stock-transfer':
        area = stockTransferArea
        break

    }
    breadcrumpData.forEach(bc => {
      if (bc.text == 'Weight Entry  /  ') bc.text = `Weight Entry - ${area} Area ${destinationArea ? `to ${destinationArea} Area` : ''} / `
      if (bc.text == 'Weight Entry') bc.text = `Weight Entry - ${area} Area ${destinationArea ? `to ${destinationArea} Area` : ''} `
    })

    setPath(breadcrumpData)
    //experiment end

    // paths = paths.map(p => {
    //   let text = p.split('-')
    //   text = text.map(t => {
    //     const txt = t.charAt(0).toUpperCase() + t.slice(1)
    //     return txt
    //   })
    //   return text.join().replace(',', ' ')
    // })
    // if (paths.length >= 3) paths[1] = `${paths[1]} - `
    // if (paths.length > 3) paths[3] = ` / ${paths[3]}`
    // if (paths.length > 4) paths[4] = ` / ${paths[4]}`
    // setPath(paths)
  }, [location.pathname])

  const dimention = document.documentElement.clientWidth
  return (
    <CHeader withSubheader className="no-shadow">
      <CSubheader className="px-3" style={{ flexWrap: "nowrap" }}>
        <CToggler inHeader className="p-0 d-lg-none" onClick={toggleSidebarMobile} />
        <div className="d-flex justify-content-between w-100">
          <CBreadcrumb className="border-0 c-subheader-nav px-0 m-0 px-0 px-md-0 w-100">
            {
              path?.map((p, i) => {
                if (path.length == (i + 1)) {
                  return <CBreadcrumbItem className={p.class}>{p.text}</CBreadcrumbItem>
                } else {
                  return <CBreadcrumbItem style={{ cursor: 'pointer' }} className={p.class} onClick={() => props.history.push(p.url)}>{dimention <= 500 && path.length > 2 ? `${i === 0 ? '   -   ' : '   /   '}` : p.text}</CBreadcrumbItem>
                }
              })
            }
          </CBreadcrumb>
          <div className="mfe-2 c-subheader-nav">
            <i className="iconU-topBarBell fontSizeWeightNumber"></i>
            <CDropdown>
              <CDropdownToggle className="no-shadow alig-items-center pr-0 d-flex align-items-center">
                <i className="iconU-topBarUser fsUserIconHeader mr-3" style={{ marginTop: "-32px" }}></i>
                <div className="mx-3 text-left email-info">
                  <p className="my-0 LatoBold">{user?.email}</p>
                  <p className="my-0 fontSizeSmallTitle">{`Daily worker - ${user?.warehouseName}`}</p>
                </div>
                <i className="iconU-sideBarArrowDown hide-m"></i>
              </CDropdownToggle>
              <CDropdownMenu>
                <div className="m-info">
                  <p className="my-0 px-3 LatoBold">{user?.email}</p>
                  <p className="my-0 px-3 fontSizeSmallTitle">{user?.warehouseName}</p>
                  <hr className="mb-0" />
                </div>
                <CDropdownItem>About</CDropdownItem>
                <CDropdownItem>How to use</CDropdownItem>
                <CDropdownItem onClick={() => props.history.push('/settings')}>Settings</CDropdownItem>
                <CDropdownItem onClick={() => userLogout({ dispatch, email: user?.email })}>Logout</CDropdownItem>
              </CDropdownMenu>
            </CDropdown>
          </div>
        </div>
      </CSubheader>
    </CHeader>
  )
}

export default TheHeader
